.popup__close {
   position: absolute;
   top: -40px;
   right: -40px;
   width: 32px;
   height: 32px;
   background-image: url(../../../images/close-Icon.svg);
   background-repeat: no-repeat;
   background-color: transparent;
   background-size: cover;
   border: none;
   cursor: pointer;
}

.popup__close:hover {
   opacity: 0.6;
}

@media screen and (max-width: 670px) {
   .popup__close {
      width: 20px;
      height: 20px;
      font-size: 18px;
      line-height: 22px;
      top: -41px;
      right: 0;
   }
}